/** UI **/
.container-loader {
  background-color: #f3f3f3;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes loaderAnimation {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
.loader::after, .loader::before, .loader {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation: loaderAnimation 1.5s infinite ease-in-out;
}

.loader::after, .loader::before {
  position: absolute;
  top: 0;
}

.loader {
  color: #4685fa;
  font-size: 13px;
  position: relative;
  animation-delay: -0.16s;
}
.loader::before {
  content: "";
  left: -3.5em;
  animation-delay: -0.32s;
}
.loader::after {
  content: "";
  left: 3.5em;
}

.header {
  overflow: hidden;
  background: url("../img/background-asaa.svg");
  background-size: cover;
  background-repeat: no-repeat;
}

.nav {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.logo {
  width: 100px;
}
.logo img {
  max-width: 100%;
}

.mobile-menu {
  display: flex;
  align-items: center;
  padding-right: 20px;
}
.mobile-menu #btn-mobile-menu {
  background-color: transparent;
  border: none;
  padding: 10px;
  font-size: 30px;
  color: #ffffff;
}
@media (min-width: 480px) {
  .mobile-menu {
    display: none;
  }
}

.menu {
  display: flex;
  opacity: 0;
  pointer-events: none;
  flex-direction: column;
  position: absolute;
  width: 100%;
  background-color: #ffffff;
  text-align: center;
  top: 100px;
}
.menu.active {
  pointer-events: all;
}
@media (min-width: 480px) {
  .menu {
    flex-direction: row;
    align-items: center;
    position: relative;
    width: auto;
    background: none;
    top: auto;
    opacity: 1;
    pointer-events: all;
  }
}
.menu .item-menu {
  padding: 15px 15px;
}
@media (min-width: 480px) {
  .menu .item-menu {
    padding: 15px 15px 5px;
  }
}
.menu .item-menu a {
  padding: 15px 15px 5px;
  color: #103d79;
  border: 1px solid transparent;
}
@media (min-width: 480px) {
  .menu .item-menu a {
    color: #ffffff;
  }
  .menu .item-menu a:hover {
    border-bottom: 1px solid #ffffff;
  }
}

.resume {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .resume {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
  }
}
.resume .description {
  color: #ffffff;
  font-size: 24px;
  text-align: center;
  align-self: center;
}
.resume .img {
  overflow: hidden;
  text-align: center;
}
.resume .img img {
  width: 80vw;
}
@media (min-width: 480px) {
  .resume .img img {
    max-width: 400px;
  }
}

#typewriter::after {
  content: "|";
  display: inline-block;
  animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
#about {
  background: #103d79;
}
#about h3 {
  font-size: 24px;
}

.about-tabs {
  display: flex;
  background-color: #103d79;
  max-width: 90%;
  padding: 20px 0 0;
  font-size: 16px;
}
.about-tabs .tab {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  padding: 15px;
  border: none;
  margin: 0;
  border-radius: 20px 10px 0 0;
}
.about-tabs .tab.active {
  background: #ffffff;
}
.about-tabs .tab .icon-tab {
  font-size: 24px;
}
.about-tabs .tab .text-tab {
  display: none;
}
@media (min-width: 480px) {
  .about-tabs .tab .text-tab {
    display: block;
  }
}

.about-container {
  padding-bottom: 20px;
}

.about-section {
  width: 100%;
  max-width: 90%;
  background-color: #1653a3;
  margin: 0 auto;
  opacity: 0;
  border: none;
  height: 0;
  pointer-events: none;
  transition: transform 0.2s ease-out;
  transform: translateX(-20%);
}

.active-section {
  opacity: 1;
  pointer-events: all;
  height: auto;
  transform: translateX(0);
  padding: 20px;
}

.inactive-section {
  opacity: 0;
  pointer-events: none;
  transform: translateX(-20%);
}

#about-info {
  color: #ffffff;
}
#about-info .description {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  #about-info .description {
    grid-template-columns: 2fr 1fr;
    text-align: justify;
  }
}
#about-info .description .btn-cv {
  display: none;
}
@media (min-width: 480px) {
  #about-info .description .btn-cv {
    display: inline-block;
  }
}
#about-info .image {
  overflow: hidden;
  max-width: 90%;
  align-self: center;
}
#about-info .btn-cv {
  padding: 20px;
  background-color: #103d79;
  color: #ffffff;
  border-radius: 15px;
}
#about-info .btn-cv:hover {
  background-color: #275189;
}
#about-info .extra {
  margin-top: 15px;
  display: flex;
  justify-content: center;
}
@media (min-width: 480px) {
  #about-info .extra {
    display: none;
  }
}

