}
#skills .icons .fa-js {
  color: #f7df1e;
}
#skills .icons .fa-wordpress {
  color: #007298;
}
#skills .icons .fa-php {
  color: #7276ac;
}
#skills .icons .fa-github {
  color: #000;
}

#services {
  color: #ffffff;
}
#services .services-container {
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  #services .services-container {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
  }
}
#services .services-container .service {
  text-align: center;
}
#services .services-container .service .title {
  font-weight: bold;
  font-size: 20px;
}
#services .services-container .service .description {
  font-size: 16px;
