}

#portfolio {
  background-color: #275189;
  padding-top: 15px;
}
#portfolio h2 {
  color: #ffffff;
}

.card {
  border: 1px solid #ffffff;
}

#container-jobs {
  padding: 10px;
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  #container-jobs {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  #container-jobs {
