  overflow: hidden;
}

.modal {
  display: none; /* Ocultar el modal de forma predeterminada */
  position: fixed; /* Permanece en su posición incluso cuando se desplaza */
  z-index: 1; /* Colocar el modal en la parte superior de todo */
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: rgba(0, 0, 0, 0.4); /* Fondo semi-transparente */
}

/* Estilo del contenido del modal */
.modal-content {
  background-color: #fefefe;
  margin: 10vh auto 0; /* Centrar vertical y horizontalmente */
  max-width: 600px;
  border-radius: 10px;
}

.modal-header {
  padding: 15px;
  border-bottom: 1px solid #ccc;
}

.modal-body {
  padding: 15px;
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 480px) {
  .modal-body {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
  }
}
.modal-body .image {
  overflow: hidden;
}
@media (min-width: 480px) {
  .modal-body .image {
    max-height: 300px;
  }
}
.modal-body .btn-project {
  display: block;
  padding: 15px;
  background-color: #103d79;
  color: #ffffff;
  border-radius: 10px;
  text-align: center;
}
.modal-body .btn-project:hover {
  background-color: #275189;
}
.modal-body .technologies {
  padding: 10px;
}
.modal-body .technologies li::before {
  content: ">";
  display: inline;
  width: 100%;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  padding: 15px;
  border-top: 1px solid #ccc;
  /* Estilo del botón de cierre */
}
.modal-footer .close-btn {
  background: #1163c0;
  color: #ffffff;
  font-size: 20px;
  padding: 10px;
  border: none;
  border-radius: 5px;
}
.modal-footer .close-btn:hover,
.modal-footer .close-btn:focus {
  background-color: #1880f7;
  text-decoration: none;
  cursor: pointer;
}

#contact {
  padding: 20px;
  background: #103d79;
}
#contact h2,
#contact p {
  text-align: center;
  color: #ffffff;
}
#contact .social {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
#contact .social .btn-social {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 15px;
  width: 40px;
  height: 40px;
  padding: 15px;
  border-radius: 50%;
  color: #ffffff;
}

.footer {
  background: #275189;
  color: #ffffff;
  padding: 20px;
}