@use "base/variables" as v;
@use "base/mixins" as m;

#typewriter::after {
    content: "|";
    display: inline-block;
    animation: blink 1s infinite;
}

@keyframes blink {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
